import moment from "moment";
import debugpkg from "debug";
const debug = debugpkg("reports");

function parseWorkdayReports(transactions) {
    if (!transactions || transactions.length <= 0) return;
    // æŠ¥å‘ŠåŒ…å«5+1è¡Œä¿¡æ¯ï¼Œ1-5å¯¹åº”å‘¨ä¸€åˆ°å‘¨äº”çš„ä¿¡æ¯ï¼Œ0è¡¨ç¤ºæ±‡æ€»
    // æ¯è¡Œä¿¡æ¯åŒ…æ‹¬ï¼šcount(äº¤æ˜“æ¬¡æ•°), win_ratio(ç›ˆåˆ©æ¯”ä¾‹)ï¼Œwin(å¹³å‡ç›ˆåˆ©é‡‘é¢)ï¼Œ
    //      loss_ratio(äºæŸæ¯”ä¾‹) ï¼Œlossï¼ˆå¹³å‡äºæŸé‡‘é¢ï¼‰ï¼Œratio_winloss(ç›ˆåˆ©äºæŸæ¯”),
    //      average(å¹³å‡äº¤æ˜“è§„æ¨¡), max_lossï¼ˆæœ€å¤§äºæŸï¼‰ï¼Œprofit(åˆ©æ¶¦)
    let results = [
        {
            count: 0,
            win_ratio: 0,
            win: 0,
            loss_ratio: 0,
            loss: 0,
            ratio_winloss: 0,
            average: 0,
            max_loss: 0,
            profit: 0,
        },
        {
            count: 0,
            win_ratio: 0,
            win: 0,
            loss_ratio: 0,
            loss: 0,
            ratio_winloss: 0,
            average: 0,
            max_loss: 0,
            profit: 0,
        },
        {
            count: 0,
            win_ratio: 0,
            win: 0,
            loss_ratio: 0,
            loss: 0,
            ratio_winloss: 0,
            average: 0,
            max_loss: 0,
            profit: 0,
        },
        {
            count: 0,
            win_ratio: 0,
            win: 0,
            loss_ratio: 0,
            loss: 0,
            ratio_winloss: 0,
            average: 0,
            max_loss: 0,
            profit: 0,
        },
        {
            count: 0,
            win_ratio: 0,
            win: 0,
            loss_ratio: 0,
            loss: 0,
            ratio_winloss: 0,
            average: 0,
            max_loss: 0,
            profit: 0,
        },
        {
            count: 0,
            win_ratio: 0,
            win: 0,
            loss_ratio: 0,
            loss: 0,
            ratio_winloss: 0,
            average: 0,
            max_loss: 0,
            profit: 0,
        },
    ];

    for (let trans of transactions) {
        let buy = trans.buy;
        // let sell = trans.sell;
        let date = moment(buy.date, "YYYYMMDD");
        let day = date.day();
        if (day < 1 && day > 5) {
            // è¶…å‡ºäº†å‘¨ä¸€ï½å‘¨äº”çš„èŒƒå›´ï¼Œè·³è¿‡è¿™ä¸ªæ—¥æœŸ
            debug(`${buy.tradeDate}äº¤æ˜“è¶…å‡ºæ˜ŸæœŸèŒƒå›´ï¼š${day}, %o`, trans);
            continue;
        }

        let days = [0, day];
        console.log(`%o`, trans);
        // console.log(
        //     `%o, ${buy.tradeDate}, ${date}, ${day}, %o %o`,
        //     trans,
        //     days,
        //     results
        // );
        for (let index of days) {
            results[index].count++;
            results[index].profit += trans.profit;
            if (trans.profit >= 0) {
                results[index].count_win++;
                results[index].win += trans.profit;
                if (results[index].max_win < trans.profit)
                    results[index].max_win = trans.profit;
            } else {
                results[index].count_loss++;
                results[index].loss += trans.profit;
                if (results[index].max_loss > trans.profit)
                    results[index].max_loss = trans.profit;
            }
        }
    }
    for (let res of results) {
        res.win_ratio = res.count_win / res.count;
        res.win = res.win / res.count_win;
        res.loss_ratio = res.count_loss / res.count;
        res.loss = res.loss / res.count_loss;
        res.ratio_winloss = res.win / res.loss;
        res.average = res.profit / res.count;
    }
    return results;
}

function showWorkdayReports(log, transactions) {
    let reports = parseWorkdayReports(transactions);
    console.log("%o", reports);

    let days = ["æ€»è®¡", "å‘¨ä¸€", "å‘¨äºŒ", "å‘¨ä¸‰", "å‘¨å››", "å‘¨äº”"];
    log(`
å·¥ä½œæ—¥    äº¤æ˜“æ¬¡æ•°    ç›ˆåˆ©æ¯”ä¾‹    å¹³å‡ç›ˆåˆ©    äºæŸæ¯”ä¾‹    å¹³å‡äºæŸ    ç›ˆäºæ¯”    å¹³å‡åˆ©æ¶¦    æœ€å¤§äºæŸ    åˆ©æ¶¦`);
    for (let report of reports) {
        log(
            `          ${report.count}    ${(report.win_ratio * 100).toFixed(
                1
            )}%    ${report.win.toFixed(2)}    ${(
                report.loss_ratio * 100
            ).toFixed(1)}%    ${report.loss.toFixed(
                2
            )}    ${report.ratio_winloss.toFixed(
                2
            )}    ${report.average.toFixed(2)}    ${report.max_loss.toFixed(
                2
            )}    ${report.profit.toFixed(2)}`
        );
    }
}

export { parseWorkdayReports, showWorkdayReports };

