/**
 * ä¸»å¤„ç†è¿‡ç¨‹
 * 1. æŸ¥çœ‹è®¾ç½®ä¸­çš„å–å‡ºæ¨¡å‹åˆ—è¡¨ï¼ŒæŒ‰åºæ‰§è¡Œï¼Œå¦‚æœæˆäº¤ï¼Œåˆ™ç›´æ¥æ¸…ç®—
 * 2. å¦‚æœæ‰§è¡Œå®Œå–å‡ºï¼Œä»ç„¶æœ‰æŒä»“ï¼Œæ£€æŸ¥é…ç½®æ˜¯å¦è®¸å¯ä¹°å…¥
 * 3. å¦‚æœéœ€è¦ä¹°å…¥ï¼ŒæŸ¥çœ‹è®¾ç½®çš„ä¹°å…¥æ¨¡å‹åˆ—è¡¨ï¼ŒæŒ‰åºæ‰§è¡Œï¼Œå¦‚æœæˆäº¤ï¼Œåˆ™ç›´æ¥æ¸…ç®—
 *
 * 2020.8.26 ç›®å‰å·²ç»æ”¯æŒæŒ‰ç…§è§„åˆ™ï¼Œéå›ºå®šå¤´å¯¸æ–¹å¼ä¸‹ï¼Œå¯ä»¥åœ¨æŒä»“ä¸‹ä»ç„¶ä¹°å…¥
 *           æŒä»“å–å‡ºæŒ‰ç…§æ¯ç¬”å•ç‹¬è¿›è¡Œï¼Œä¸åˆå¹¶è¿›è¡Œ
 *
 * @param {*} index å½“å‰æ—¥è‚¡ç¥¨æ•°æ®ç´¢å¼•
 * @param {*} stockData è‚¡ç¥¨æ•°æ®ä¿¡æ¯
 * @param {*} capitalData è´¦æˆ·ä¿¡æ¯
 * @param {*} options ç®—æ³•å‚æ•°
 */
async function executeTransaction(index, stockData, capitalData, options) {
    let translog = null;
    // é¦–å…ˆæ£€æŸ¥å–å‡º
    // æ‰€æœ‰ç®—æ³•é¦–å…ˆæ£€æŸ¥å¹¶å¤„ç†æ­¢æŸ
    // æ£€æŸ¥æ˜¯å¦éœ€è¦æ­¢æŸ
    let tradeDate = stockData[index].trade_date;
    let stockInfo = capitalData.info;

    let sellRules = options.rules && options.rules.sell;
    let buyRules = options.rules && options.rules.buy;

    // ç›®å‰çš„æŒä»“æƒ…å†µ
    let stocks = capitalData && capitalData.stocks;
    if (sellRules) {
        // æ¯ä¸ªä¹°å…¥æŒè‚¡å•ç‹¬å¤„ç†
        let stockId = 0;
        while (stockId < stocks.length) {
            let stock = stocks[stockId];
            debug(`å–å‡ºè‚¡ç¥¨ä¿¡æ¯: %o`, stock);

            let sold = false;
            for (let rule of sellRules) {
                if (rule) {
                    debug(
                        `${rule.name} å–å‡ºæ£€æŸ¥ï¼š${tradeDate}, %o`,
                        stockData[index]
                    );
                    translog = rule.checkSellTransaction(
                        stockInfo,
                        stock,
                        index,
                        stockData,
                        options
                    );
                    if (translog) translog.transeq = stock.transeq;
                    if (
                        executeCapitalSettlement(
                            stockInfo,
                            translog,
                            capitalData,
                            options
                        )
                    ) {
                        debug(
                            `${
                                rule.name
                            } å–å‡ºï¼š${tradeDate}ï¼Œä»·æ ¼ï¼š${formatFxstr(
                                translog.price
                            )}å…ƒï¼Œæ•°é‡ï¼š${
                                translog.count / 100
                            }æ‰‹ï¼Œæ€»ä»·ï¼š${translog.total.toFixed(
                                2
                            )}å…ƒ[ä½£é‡‘${translog.commission.toFixed(
                                2
                            )}å…ƒï¼Œè¿‡æˆ·è´¹${translog.fee.toFixed(
                                2
                            )}ï¼Œå°èŠ±ç¨${translog.duty.toFixed(2)}å…ƒ], ${
                                translog.memo
                            }`
                        );
                        sold = true;
                        break;
                    }
                }
            }
            if (!sold) {
                // æ²¡æœ‰å–å‡ºï¼Œéœ€è¦æŸ¥çœ‹ä¸‹ä¸€æ¡æŒè‚¡è¿›è¡Œæ£€æŸ¥
                stockId++;
            }
        }
    }

    // å¦‚æœéå›ºå®šå¤´å¯¸ï¼Œåˆ™æ£€æŸ¥æ˜¯å¦æœ‰æŒä»“ï¼Œå¦‚æœæœ‰ä¸è¿›è¡Œä¹°å…¥
    if (!options.fixCash && capitalData.stocks.length > 0) return;
    // if (capitalData && capitalData.stock && capitalData.stock.count > 0) return;

    // æ‰§è¡Œä¹°å…¥
    // debug("æ‰§è¡Œä¹°å…¥æ£€æŸ¥");
    let cash = capitalData.balance;
    if (options.fixCash) cash = options.initBalance;
    if (buyRules) {
        for (let rule of buyRules) {
            translog = rule.checkBuyTransaction(
                stockInfo,
                cash,
                index,
                stockData,
                options
            );
            if (translog) translog.transeq = capitalData._transeq++;
            // debug(`ä¹°å…¥ç»“æœï¼š%o`, translog);
            if (
                executeCapitalSettlement(
                    stockInfo,
                    translog,
                    capitalData,
                    options
                )
            ) {
                debug(
                    `${
                        rule.name
                    } ä¹°å…¥äº¤æ˜“ï¼š${tradeDate}ï¼Œä»·æ ¼ï¼š${translog.price.toFixed(
                        2
                    )}å…ƒï¼Œæ•°é‡ï¼š${
                        translog.count / 100
                    }æ‰‹ï¼Œæ€»ä»·ï¼š${translog.total.toFixed(
                        2
                    )}å…ƒ[ä½£é‡‘${translog.commission.toFixed(
                        2
                    )}å…ƒï¼Œè¿‡æˆ·è´¹${translog.fee.toFixed(
                        2
                    )}ï¼Œå°èŠ±ç¨${translog.duty.toFixed(2)}å…ƒ], ${translog.memo}`
                );
                // debug(`è‚¡ç¥¨ä¿¡æ¯ï¼š%o`, stockInfo);
                // debug(`è´¦æˆ·ä¿¡æ¯ï¼š%o`, capitalData);
                // return translog;
            }
        }
    }
}

/**
 * æ ¹æ®äº¤æ˜“è®°å½•å®Œæˆè´¦æˆ·æ¸…ç®—
 * @param {*} stockInfo è‚¡ç¥¨ä¿¡æ¯
 * @param {*} translog äº¤æ˜“è®°å½•
 * @param {*} capitalData è´¦æˆ·æ•°æ®
 * @param {*} options é…ç½®å‚æ•°
 */
function executeCapitalSettlement(stockInfo, translog, capitalData, options) {
    // debug(`æ‰§è¡Œæ¸…ç®— %o`, translog);
    if (_.isEmpty(translog)) return false;
    // å¦‚æœéå›ºå®šå¤´å¯¸ï¼Œæ£€æŸ¥å½“å‰æä¾›çš„äº¤æ˜“ä½™é¢æ˜¯å¦å¯æ‰§è¡Œ
    if (!options.fixCash && translog.total + capitalData.balance < 0) {
        debug(
            `è´¦æˆ·ä½™é¢${capitalData.balance}ä¸è¶³(${
                translog.total
            })ï¼Œæ— æ³•å®Œæˆæ¸…ç®—ï¼Œäº¤æ˜“å–æ¶ˆ! äº¤æ˜“ä¿¡æ¯: ${
                translog.type === "buy" ? "ä¹°å…¥" : "å–å‡º"
            }${stockInfo.ts_code} ${translog.count}è‚¡ï¼Œä»·æ ¼${
                translog.price
            }ï¼Œå…±è®¡${translog.total}å…ƒ[å«ä½£é‡‘${translog.commission}å…ƒï¼Œè¿‡æˆ·è´¹${
                translog.fee
            }ï¼Œå°èŠ±ç¨${translog.duty}å…ƒ]`
        );
        return false;
    }

    // å¤„ç†äº¤æ˜“ä¿¡æ¯
    capitalData.balance += translog.total;
    // å¦‚æœå½“å‰ä¹°å…¥ï¼Œstockä¸­æ”¾ç½®æŒè‚¡ä¿¡æ¯å’Œä¹°å…¥äº¤æ˜“æ—¥å¿—ï¼Œåªæœ‰å–å‡ºå‘ç”Ÿæ—¶æ‰åˆå¹¶ç”Ÿæˆä¸€æ¡äº¤æ˜“è®°å½•ï¼ŒåŒ…å«ä¸¤ä¸ªéƒ¨åˆ†
    if (translog.type === "buy") {
        capitalData.stocks.push({
            transeq: translog.transeq,
            count: translog.count,
            price: translog.price,
            buy: translog,
        });
    } else {
        let stock;
        for (let i = 0; i < capitalData.stocks.length; i++) {
            if (capitalData.stocks[i].transeq === translog.transeq) {
                stock = capitalData.stocks[i];
                capitalData.stocks.splice(i, 1);
                break;
            }
        }
        if (!stock) {
            debug(
                `æ²¡æœ‰æ‰¾åˆ°è¦æ‰§è¡Œçš„äº¤æ˜“åºå·ï¼š${translog.transeq}, %o`,
                capitalData.stocks
            );
            return false;
        }

        let settledlog = {
            transeq: stock.transeq,
            tradeDate: translog.tradeDate,
            profit: stock.buy.total + translog.total,
            income: translog.count * translog.price - stock.count * stock.price,
            buy: stock.buy,
            sell: translog,
        };
        // capitalData.stock = {
        //     //info: null,
        //     count: 0,
        //     price: 0,
        // };
        capitalData.transactions.push(settledlog);
    }
    // debug("å®Œæˆæ¸…ç®—ï¼");
    return true;
}

/**
 * åˆ›å»ºæŒ‡å®šæ—¥æœŸå’Œè‚¡ç¥¨ä¿¡æ¯çš„å–å‡ºäº¤æ˜“
 * @param {*} stockInfo
 * @param {*} tradeDate
 * @param {*} tradeDateIndex
 * @param {*} count
 * @param {*} price
 * @param {*} memo
 */
function createSellTransaction(
    stockInfo,
    tradeDate,
    tradeDateIndex,
    count,
    price,
    methodType,
    memo
) {
    // è®¡ç®—è´¹ç”¨
    let total = calculateTransactionFee(false, stockInfo, count, price);
    // åˆ›å»ºå–å‡ºäº¤æ˜“è®°å½•
    return {
        date: tradeDate,
        dateIndex: tradeDateIndex,
        type: "sell",
        count,
        price,
        total: total.total,
        amount: total.amount,
        fee: total.fee,
        commission: total.commission,
        duty: total.duty,
        methodType,
        memo,
    };
}

/**
 * æ„å»ºä¹°å…¥äº¤æ˜“ä¿¡æ¯
 * @param {*} stockInfo è‚¡ç¥¨ä¿¡æ¯
 * @param {*} tradeDate äº¤æ˜“æ—¥æœŸ
 * @param {*} tradeDateIndex äº¤æ˜“æ—¥æœŸç´¢å¼•ï¼ˆæ–¹ä¾¿ç”¨äºè®¡ç®—äº¤æ˜“æ—¥æ•°ï¼‰
 * @param {*} balance å¯ç”¨ä½™é¢
 * @param {*} price ä¹°å…¥ä»·æ ¼
 * @param {*} memo äº¤æ˜“å¤‡æ³¨
 */
function createBuyTransaction(
    stockInfo,
    tradeDate,
    tradeDateIndex,
    balance,
    price,
    methodType,
    memo
) {
    // è®¡ç®—è´¹ç”¨
    let count = parseInt(balance / price / 100) * 100;
    // æœ€å°äº¤æ˜“å•ä½ä¸º1æ‰‹ï¼Œèµ„é‡‘ä¸è¶³æ”¾å¼ƒï¼
    if (count < 100) return;
    let total = calculateTransactionFee(true, stockInfo, count, price);
    while (total.total + balance < 0) {
        count -= 100;
        if (count < 100) return;
        total = calculateTransactionFee(true, stockInfo, count, price);
    }
    // åˆ›å»ºä¹°å…¥äº¤æ˜“è®°å½•
    return {
        date: tradeDate,
        dateIndex: tradeDateIndex,
        type: "buy",
        count: count,
        price,
        total: total.total,
        amount: total.amount,
        fee: total.fee,
        commission: total.commission,
        duty: total.duty,
        methodType,
        memo,
    };
}

/**
 * è®¡ç®—äº¤æ˜“ä»·æ ¼å’Œè´¹ç”¨
 * @param {boolean}} buy ä¹°å–æ ‡è®°
 * @param {*} stockInfo è‚¡ç¥¨ä¿¡æ¯
 * @param {*} count ä¹°å–æ•°é‡
 * @param {*} price ä¹°å–å•ä»·
 */
function calculateTransactionFee(buy, stockInfo, count, price) {
    let amount = count * price;
    let commission = (amount * 0.25) / 1000;
    let fee = 0.0;
    let duty = 0.0;
    if (stockInfo.exchange === "SSE") {
        // ä¸Šæµ·ï¼Œè¿‡æˆ·è´¹åƒåˆ†ä¹‹0.2
        fee += (amount * 0.02) / 1000;
    } else if (stockInfo.exchange === "SZSE") {
        // æ·±åœ³ï¼Œæ— 
    }
    // å°èŠ±ç¨ï¼Œä»…å¯¹å–æ–¹æ”¶å–
    if (!buy) {
        duty += (amount * 1) / 1000;
    }

    let total = 0.0;
    if (buy) {
        total = 0 - (amount + commission + fee + duty);
    } else {
        total = amount - commission - fee - duty;
    }

    return { total, amount, commission, fee, duty };
}

function parseCapitalReports(capitalData) {
    if (_.isEmpty(capitalData)) return;
    // è´¦æˆ·ä¿¡æ¯ä¸­ä¸»è¦éœ€åˆ†æäº¤æ˜“è¿‡ç¨‹ï¼Œæ­£å¸¸éƒ½æ˜¯ä¸ºä¸€æ¬¡ä¹°å…¥ï¼Œä¸€æ¬¡å–å‡ºï¼Œè¿™æ ·ä½œä¸ºä¸€ç»„äº¤æ˜“ï¼Œè·å¾—ä¸€æ¬¡ç›ˆåˆ©ç»“æœ
    let count = capitalData.transactions.length;
    let count_win = 0;
    let total_win = 0;
    let count_loss = 0;
    let total_loss = 0;
    let total_profit = 0;
    let total_fee = 0;
    let max_profit = 0;
    let max_loss = 0;
    let average_profit = 0;
    let average_win = 0;
    let average_loss = 0;
    let max_wintimes = 0; // è¿ç»­ç›ˆåˆ©æ¬¡æ•°
    let max_losstimes = 0; // è¿ç»­äºæŸæ¬¡æ•°
    let max_windays = 0;
    let max_lossdays = 0;
    let average_windays = 0;
    let average_lossdays = 0;
    // {times: æ€»æ¬¡æ•°, win_times: ç›ˆåˆ©æ¬¡æ•°, loss_times: æŸå¤±æ¬¡æ•°}
    let selltypes = {};
    //let selltype_times = {};

    // æ”¶ç›Šç‡ï¼šè¡¨ç¤ºå•ä½æˆæœ¬çš„æ”¶å…¥æ¯”ä¾‹
    let ror_win = 0;
    let ror_loss = 0;
    let ror = 0;

    let tmp_cost = 0;
    let tmp_cost_win = 0;
    let tmp_cost_loss = 0;

    let currentType = 0;
    let tmp_times = 0;
    let tmp_windays = 0;
    let tmp_lossdays = 0;
    for (let log of capitalData.transactions) {
        let days = log.sell.dateIndex - log.buy.dateIndex + 1;

        let selltype = selltypes[log.sell.methodType];
        if (!selltype) {
            selltypes[log.sell.methodType] = {
                times: 0,
                win_times: 0,
                loss_times: 0,
            };
        }
        selltypes[log.sell.methodType].times += 1;

        if (log.profit >= 0) {
            count_win++;
            total_win += log.profit;
            tmp_cost_win += -log.buy.total;
            if (max_profit < log.profit) max_profit = log.profit;

            tmp_windays += days;
            if (max_windays < days) max_windays = days;

            // è¿ç»­è®¡æ•°
            if (currentType === 1) {
                tmp_times++;
            } else {
                if (currentType === -1) {
                    if (max_losstimes < tmp_times) max_losstimes = tmp_times;
                }
                // åˆå§‹åŒ–
                currentType = 1;
                tmp_times = 1;
            }

            selltypes[log.sell.methodType].win_times += 1;
        } else {
            count_loss++;
            total_loss += log.profit;
            tmp_cost_loss += -log.buy.total;
            if (max_loss > log.profit) max_loss = log.profit;

            tmp_lossdays += days;
            if (max_lossdays < days) max_lossdays = days;

            // è¿ç»­è®¡æ•°
            if (currentType === -1) {
                tmp_times++;
            } else {
                if (currentType === 1) {
                    if (max_wintimes < tmp_times) max_wintimes = tmp_times;
                }
                // åˆå§‹åŒ–
                currentType = -1;
                tmp_times = 1;
            }

            selltypes[log.sell.methodType].loss_times += 1;
        }

        total_profit += log.profit;
        total_fee +=
            log.buy.commission +
            log.buy.fee +
            log.buy.duty +
            (log.sell.commission + log.sell.fee + log.sell.duty);
        tmp_cost += -log.buy.total;
    }

    if (currentType === 1) {
        if (max_wintimes < tmp_times) max_wintimes = tmp_times;
    } else if (currentType === -1) {
        if (max_losstimes < tmp_times) max_losstimes = tmp_times;
    }

    average_profit = total_profit / count;
    average_win = total_win / count_win;
    average_loss = -total_loss / count_loss;

    average_windays = Number((tmp_windays / count_win).toFixed(1));
    average_lossdays = Number((tmp_lossdays / count_loss).toFixed(1));

    ror = total_profit / tmp_cost;
    ror_win = total_win / tmp_cost_win;
    ror_loss = total_loss / tmp_cost_loss;

    return {
        count,
        total_profit,
        total_fee,
        count_win,
        total_win,
        count_loss,
        total_loss,
        max_profit,
        max_loss,
        average_profit,
        average_win,
        average_loss,
        max_wintimes,
        max_losstimes,
        max_windays,
        max_lossdays,
        average_windays,
        average_lossdays,
        selltypes,

        ror,
        ror_win,
        ror_loss,
    };
}

