    //      market: 'ä¸»æ¿', exchange: 'SZSE',
    //      area: 'æ·±åœ³', industry: 'é“¶è¡Œ', fullname: 'å¹³å®‰é“¶è¡Œè‚¡ä»½æœ‰é™å…¬å¸',
    //      enname: 'Ping An Bank Co., Ltd.', curr_type: 'CNY',
